.navbar-brand {
    float: left;
    height: 50px;
    padding: 5px 15px;
    font-size: 18px;
    line-height: 20px;

    &:hover {
     	background-color: #eee!important;
    }
}

.dashboard-navbar {
	margin-bottom: 0px!important;
	background-color: #e5e5e5;
	margin-bottom: 50px;
	box-shadow: 0px 1px 10px #202020;
}

.mainHeader {
	background-color: #4285F4;
	margin-bottom: 30px;
	padding-bottom: 10px;
	color: white;
	text-transform: uppercase;
	box-shadow: 0px 1px 10px #202020;

	h4 {
	  font-weight: 500;
	  letter-spacing: 1px;
	  margin-top: 20px;
	}
}



.navbar-default .nav > li>a, .navbar-default .nav>li>a:focus {
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    //color: #222;
}

.btn-primary .badge {
    color: #f05f40;
    background-color: #fff;
}

.btn {
    border-radius: 300px;
    text-transform: uppercase;
    font-family: 'Open Sans','Helvetica Neue',Arial,sans-serif;
    font-weight: 700;
    font-style: 12px;
    padding: 7px 14px;
}

.panel {
	.panel-heading {
		text-transform: uppercase;
		font-weight: 500;
	}
}